<template>
  <div class="container">
    <div class="login">
      <h1 class="title">哗啦啦老板通</h1>
      <el-input v-model="phoneNumber" placeholder="手机号" @blur="handleInputBlur" class="phone"></el-input>
      <el-row :gutter="25" class="verify-box">
        <el-col :span="15"><el-input v-model="verify" placeholder="验证码" class="verify"></el-input></el-col>
        <el-col :span="9"><el-button class="verify-btn" @click="handleVerifyClick">获取验证码</el-button></el-col>
      </el-row>
      <div class="login-btn-box">
        <h4 class="alert-text">{{alertText}}</h4>
        <el-button class="login-btn" @click="handleLogin">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint no-console: 0 */
import axios from 'axios'
import qs from 'qs'
import { mapMutations } from 'vuex'

export default {
  name: 'login',
  data() {
    return {
      phoneNumber: '',
      verify: '',
      alertText: '',
      phoneFlag: true,
    }
  },
  methods: {
    ...mapMutations(['SET_USER']),
    handleInputBlur() {
      this.alertText = ''
      if (!(/^1\d{10}$/.test(this.phoneNumber))) {
        this.alertText = '客官，手机号不正确，重新输入吧~'
        this.phoneFlag = false
      } else {
        this.phoneFlag = true
      }
    },
    handleVerifyClick() {
      this.handleInputBlur()
      if (this.phoneFlag) {
        axios.get('/sms/code', {
          params: {
            phoneNumber: this.phoneNumber,
          },
        })
          .then((res) => {
            if (res && res.data) {
              this.alertText = res.data.resultMsg || res.data.msg
            }
          })
          .catch(() => {
            this.alertText = '出错啦，刷新重试一下吧~'
            throw new Error()
          })
      }
    },
    handleLogin() {
      this.handleInputBlur()
      if (this.phoneFlag) {
        axios.post('/sms/verify', qs.stringify({
          code: this.verify,
          tel: this.phoneNumber,
        }))
          .then((res) => {
            if (res && res.data) {
              if (res.data.code === '000' && res.data.success) {
                this.SET_USER({ name: this.phoneNumber })
                this.$router.push('/home/advertise')
              } else {
                this.alertText = res.data.resultMsg || res.data.msg
              }
            }
          })
          .catch(() => {
            this.alertText = '出错啦，刷新重试一下吧~'
          })
      }
    },
  },
}
</script>

<style lang="scss" scoped>
  .container {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #26374C;
  }

  .login {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 100px;
    padding: 30px 30px 50px;
    width: 340px;
    border-radius: 8px;
    background-color: rgba(255, 255, 255, 0.1);

    .title {
      margin-bottom: 10px;
      font-size: 30px;
      text-align: center;
      color: #fff;
    }

    .phone {
      margin: 30px auto;
    }

    .verify-box {
      margin-bottom: 30px;
      .verify-btn {
        background: #4a82a5;
        border-color: #4a82a5;
        color: #fff;
        &:hover {
          background: #3c6f8f;
          border-color: #3c6f8f;
        }
      }
    }

    .login-btn-box {
      .alert-text {
        padding-bottom: 10px;
        height: 12px;
        text-align: center;
        color: #f99;
      }

      .login-btn {
        width: 340px;
        background: #009989;
        border-color: #009989;
        color: #fff;
        &:hover {
          background: #048073;
          border-color: #048073;
        }
      }
    }

  }
</style>



