import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/pages/Home'
import Login from '@/components/common/Login'
import Advertise from '@/components/pages/Advertise'
import SetGroup from '@/components/pages/SetGroup'
import FeedBack from '@/components/pages/FeedBack'
import DataSource from '@/components/pages/DataSource'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/manage',
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      children: [
        {
          path: 'advertise',
          name: 'Advertise',
          component: Advertise,
        },
        {
          path: 'setgroup',
          name: 'SetGroup',
          component: SetGroup,
        },
        {
          path: 'feedback',
          name: 'FeedBack',
          component: FeedBack,
        },
        {
          path: 'datasource',
          name: 'DataSource',
          component: DataSource,
        },
      ],
    },
  ],
})



