<template>
  <el-header>
    <el-button class="nav-btn" icon="el-icon-menu" size="mini" @click="handleNavCollapse"></el-button>
    <div class="user-operation">
      <el-dropdown>
        <i class="el-icon-setting" style="margin-right: 15px"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>查看</el-dropdown-item>
          <el-dropdown-item>新增</el-dropdown-item>
          <el-dropdown-item>删除</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    <span>{{ this.userInfo ? this.userInfo.name : '无名氏' }}</span>
    </div>
  </el-header>
</template>

<script>
/* eslint no-console: 0 */
import { mapState, mapMutations } from 'vuex'

export default {
  computed: {
    ...mapState(['userInfo']),
  },
  methods: {
    ...mapMutations({
      changeNavigation: 'CHANGE_NAVIGATION',
    }),
    handleNavCollapse() {
      this.changeNavigation()
    },
  },
}
</script>

<style scoped lang="scss">
  .el-header {
    line-height: 60px;
    font-size: 14px;
    background-color: #fff;
    color: #333;
    border-bottom: 1px solid #d9d9d9;
    .nav-btn {
      color: #fff;
      background-color: #1ab394;
      border: none;
    }
    .user-operation {
      float: right;
      padding-right: 30px;
    }
  }
</style>



