<template>
  <div class="table-container">
      <el-input class="input" v-model="groupID"></el-input>
      <el-button @click="setGroup()">设置</el-button>
  </div>
</template>

<script>
/* eslint no-console: 0 */
/* eslint no-alert: 0 */
/* eslint consistent-return: 0 */
import axios from 'axios';
import qs from 'qs';

export default {
  data() {
    return {
      groupID: 0,
    };
  },
  methods: {
    /** 查询当前穿透的集团号 */
    getGroup() {
      axios
        .post('/management/group/getGroupID', qs.stringify({}))
        .then((res) => {
          // debugger;
          if (res.data.resultCode === '000') {
            if (res.data.data) {
              this.groupID = res.data.data;
            } else {
              this.$message({
                message: '返回内容为空',
                type: 'warning',
              });
            }
          } else {
            this.$message.error(res.msg || '请求失败');
          }
        })
        .catch(() => {
          throw new Error();
        });
    },
    /** 设置穿透集团 */
    setGroup() {
      axios
        .post('/management/group/setGroupID', qs.stringify({ groupID: this.groupID }))
        .then((res) => {
          // debugger;
          if (res.data.code === '000') {
            this.$message({
              message: '设置成功',
              type: 'success',
            });
          } else {
            this.$message.error(res.msg || '设置失败');
          }
        })
        .catch(() => {
          throw new Error();
        });
    },
  },
  created() {
    this.getGroup();
  },
};
</script>

<style scoped lang="scss">
.input {
  width: 200px;
}
</style>



