<template>
  <div class="table-container">
    <el-form :inline="true" class="demo-form-inline">
  <el-form-item label="集团ID">
    <el-input v-model="queryTableParams.groupIDParam" placeholder="集团ID"></el-input>
  </el-form-item>
  <el-form-item label="手机类型">
    <el-select v-model="queryTableParams.mobileType" placeholder="">
      <el-option selected = "selected" label="全部" value="0"></el-option>
      <el-option label="Android" value="1"></el-option>
      <el-option label="iOS" value="2"></el-option>
    </el-select>
  </el-form-item>
    <el-form-item label="版本号">
    <el-input v-model="queryTableParams.version" placeholder="版本号"></el-input>
  </el-form-item>
      <el-form-item label="反馈渠道">
    <el-select v-model="queryTableParams.channel" placeholder="">
      <el-option selected = "selected" label="全部" value="0"></el-option>
      <el-option label="首页反馈" value="1"></el-option>
      <el-option label="消息中心反馈" value="2"></el-option>
      <el-option label="经营分析反馈" value="3"></el-option>
    </el-select>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click=getPageData()>查询</el-button>
  </el-form-item>
    <el-form-item>
    <el-button @click=clearQueryParams()>清空</el-button>
  </el-form-item>
</el-form>
    <el-table
      :data="tableData"
      border
      style="border-radius: 10px;">
      <el-table-column prop="groupID" label="集团ID" width="80">
      </el-table-column>
      <el-table-column prop="identity" label="反馈者身份" width="100">
      </el-table-column>
      <el-table-column prop="connectionInfo" label="联系方式" width="140">
      </el-table-column>
      <el-table-column prop="mobileType" label="来源" width="80">
      </el-table-column>
      <el-table-column prop="version" label="版本" width="80">
      </el-table-column>
      <el-table-column prop="channel" label="反馈渠道" width="120">
      </el-table-column>
      <el-table-column prop="message" label="反馈内容">
      </el-table-column>
      <el-table-column prop="createTime" label="反馈日期" width="160">
      </el-table-column>
      <el-table-column
      fixed="right"
      label="操作"
      width="100">
      <template slot-scope="scope">
        <el-button @click=openFeedDialog(scope.row) type="text" size="medium">{{getFeedBackBtnName(scope.row)}}</el-button>
      </template>
    </el-table-column>
    </el-table>
    <el-dialog title="反馈" :visible.sync="dialogFeedVisible" class="feed-dialog">
  <el-form>
    <el-form-item label="吐槽内容：" label-width="100px">
      <span>{{tucaoMessage}}</span>
    </el-form-item>
    <el-form-item label="反馈内容：" label-width="100px">
      <el-input type="textarea" v-model="feedbackParam.msg" autocomplete="off"></el-input>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFeedVisible = false">取 消</el-button>
    <el-button type="primary" @click=reply()>确 定</el-button>
  </div>
</el-dialog>
    <el-pagination
      background
      layout="prev, pager, next, jumper"
      :page-count="parseInt(this.pageQuery.pageCount)"
      :current-page="parseInt(this.pageQuery.pageNo)"
      @current-change="handleCurrentChange"
      >
    </el-pagination>
  </div>
</template>

<script>
/* eslint no-console: 0 */
/* eslint no-alert: 0 */
/* eslint consistent-return: 0 */
import axios from 'axios'
import qs from 'qs'

export default {
  data() {
    const pageQuery = {
      pageNo: 1,
      pageSize: 15,
      pageCount: 0,
      totalSize: 0,
    }
    return {
      tableData: null,
      pageQuery,
      dialogFeedVisible: false,
      feedbackInfo: '',
      feedbackParam: {
        itemID: '',
        groupID: '',
        userID: '',
        title: '吐槽反馈',
        msg: '', // 反馈内容
      },
      tucaoMessage: '',
      queryTableParams: {
        mobileType: '0',
        version: '',
        groupIDParam: '0',
        channel: '0',
      },
    }
  },
  methods: {
    handleCurrentChange(pageNo) {
      this.getPageData(pageNo)
    },
    /**
     * 分页查询
     */
    getPageData(pageNo) {
      axios.post('/management/feedback/queryFeedBackByPage',
        qs.stringify({
          pageNo: pageNo || this.pageQuery.pageNo,
          pageSize: this.pageQuery.pageSize,
          mobileType: this.queryTableParams.mobileType,
          version: this.queryTableParams.version,
          groupIDParam: this.queryTableParams.groupIDParam,
          channel: this.queryTableParams.channel,

        })
      ).then((res) => {
        if (res.data.data) {
          const info = res.data.data
          if (info.feedBackDetailModelList) this.tableData = info.feedBackDetailModelList
          if (info.pageInfo) this.pageQuery = info.pageInfo
        }
      })
        .catch(() => {
          throw new Error()
        })
    },
    /**
     * 反馈按钮名称
     */
    getFeedBackBtnName(row) {
      if (row.resultNum && row.resultNum > 0) {
        return '再次反馈';
      }
      return '反馈';
    },
    /**
     * 清空参数
     */
    clearQueryParams() {
      this.queryTableParams.mobileType = '0';
      this.queryTableParams.version = '';
      this.queryTableParams.groupIDParam = '0';
      this.queryTableParams.channel = '0';
    },
    /**
     * 打开反馈弹窗
     */
    openFeedDialog(row) {
      this.dialogFeedVisible = true;
      this.feedbackParam.itemID = row.itemID;
      this.feedbackParam.groupID = row.groupID;
      this.feedbackParam.userID = row.userID;
      this.feedbackParam.msg = '';
      this.tucaoMessage = row.message;
    },
    /**
     * 请求反馈接口
     */
    reply() {
      axios.post('/management/feedback/insertFeedBackInfo',
        qs.stringify(this.feedbackParam)
      ).then((res) => {
        if (res.data.resultMsg && res.data.resultMsg === 'success') {
          this.$message({
            message: res.data.data || '接口请求成功，无返回消息，联系开发',
            type: 'success',
          });
          this.dialogFeedVisible = false;
        }
      })
        .catch(() => {
          throw new Error()
        })
    },
  },
  created() {
    this.getPageData()
  },
}
</script>

<style scoped lang="scss">
  .table-container {
    box-sizing: border-box;
    padding: 20px;
    min-width: 100%;
    min-height: 100%;
    background-color: #fff;
    .el-table {
      td {
        padding: 10px 0;
      }
    }
    .el-pagination {
      padding: 10px 10px 0;
      text-align: right;
      .el-pager li:not(.disabled).active {
        background-color: #1ab394;
      }
    }
    .el-button--primary {
      background-color: #1ab394;
      border-color: #1ab394;
    }
  }
</style>



