<template>
  <div class="table-container">
    <el-table
      :data="tableData"
      border
      style="border-radius: 10px;">
      <el-table-column prop="title" label="广告标题">
      </el-table-column>
      <el-table-column prop="pv" label="PV">
      </el-table-column>
      <el-table-column prop="uv" label="UV">
      </el-table-column>
      <el-table-column prop="statisticsDate" label="统计日期">
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next, jumper"
      :page-count="parseInt(this.pageQuery.pageCount)"
      :current-page="parseInt(this.pageQuery.pageNo)"
      @current-change="handleCurrentChange"
      >
    </el-pagination>
  </div>
</template>

<script>
/* eslint no-console: 0 */
/* eslint no-alert: 0 */
/* eslint consistent-return: 0 */
import axios from 'axios'
import qs from 'qs'

export default {
  data() {
    const pageQuery = {
      pageNo: 1,
      pageSize: 15,
      pageCount: 0,
      totalSize: 0,
    }
    return {
      tableData: null,
      pageQuery,
    }
  },
  methods: {
    handleCurrentChange(pageNo) {
      this.getPageData(pageNo)
    },
    getPageData(pageNo) {
      axios.post('/management/advertise/statistics',
        qs.stringify({
          pageNo: pageNo || this.pageQuery.pageNo,
          pageSize: this.pageQuery.pageSize,
        })
      )
        .then((res) => {
          if (res.data.data) {
            const info = res.data.data
            if (info.infoList) this.tableData = info.infoList
            if (info.pageInfo) this.pageQuery = info.pageInfo
          }
        })
        .catch(() => {
          throw new Error()
        })
    },
  },
  created() {
    this.getPageData()
  },
}
</script>

<style scoped lang="scss">
  .table-container {
    box-sizing: border-box;
    padding: 20px;
    min-width: 100%;
    min-height: 100%;
    background-color: #fff;
    .el-table {
      td {
        padding: 10px 0;
      }
    }
    .el-pagination {
      padding: 10px 10px 0;
      text-align: right;
      .el-pager li:not(.disabled).active {
        background-color: #1ab394;
      }
    }
  }
</style>



