<template>
  <el-aside width="auto">
    <div class="logo">{{ this.isCollapse ? null : '哗啦啦老板通'}}</div>
    <el-menu
      router
      default-active="/home/advertise"
      class="el-menu-vertical"
      background-color="#2f4050"
      text-color="#fff"
      active-text-color="#ffd04b"
      :collapse="isCollapse">
      <el-menu-item index="/home/advertise">
        <i class="el-icon-news"></i>
        <span slot="title">广告统计</span>
      </el-menu-item>
      <el-menu-item index="/home/feedback">
        <i class="el-icon-edit-outline"></i>
        <span slot="title">吐槽反馈</span>
      </el-menu-item>
      <el-menu-item index="/home/setgroup">
        <i class="el-icon-setting"></i>
        <span slot="title">集团设置</span>
      </el-menu-item>
      <el-menu-item index="/home/datasource">
        <i class="el-icon-refresh"></i>
        <span slot="title">数据源切换</span>
      </el-menu-item>
    </el-menu>
  </el-aside>
</template>

<script>
/* eslint no-console: 0 */
import { mapState } from 'vuex'

export default {
  computed: {
    ...mapState(['isCollapse']),
  },
}
</script>

<style scoped lang="scss">
  .el-aside {
    position: relative;
    box-sizing: border-box;
    padding-top: 60px;
    background-color: #2f4050;
    .logo {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 60px;
      line-height: 60px;
      text-align: center;
      font-size: 20px;
      color: #fff;
    }
    .el-menu-vertical {
      height: 100%;
      overflow: auto;
      font-size: 10px;
      border: none;
      &:not(.el-menu--collapse) {
        width: 200px;
      }
    }
  }
</style>



