<template>
  <div class="table-container">
    <div>
      <div  style="display:inline">
        <el-radio style="background:white" v-model="datasourceType" label="1" border>TiDB</el-radio>
        <el-radio style="background:white" v-model="datasourceType" label="2" border>MySQL</el-radio>
      </div>
          <el-button @click="setDatasource()">切换</el-button>
          <span>非开发人员请勿点击此按钮！！！</span>
      </div>
  </div>
</template>

<script>
/* eslint no-console: 0 */
/* eslint no-alert: 0 */
/* eslint consistent-return: 0 */
import axios from 'axios';
import qs from 'qs';

export default {
  data() {
    return {
      datasourceType: '0',
    };
  },
  methods: {
    /** 查询当前数据源类型 */
    getDatasource() {
      axios
        .post('/management/group/getDataSourceType', qs.stringify({}))
        .then((res) => {
          if (res.data.resultCode === '000') {
            if (res.data.data) {
              this.datasourceType = res.data.data.toString();
            } else {
              this.$message({
                message: '返回内容为空',
                type: 'warning',
              });
            }
          } else {
            this.$message.error(res.msg || '请求失败');
          }
        })
        .catch(() => {
          throw new Error();
        });
    },
    /** 设置数据源类型 */
    setDatasource() {
      axios
        .post('/management/group/setDataSourceType', qs.stringify({ datasourceType: this.datasourceType }))
        .then((res) => {
          if (res.data.code === '000') {
            this.$message({
              message: '数据源切换成功',
              type: 'success',
            });
          } else {
            this.$message.error(res.data.msg || '数据源切换失败');
          }
        })
        .catch(() => {
          throw new Error();
        });
    },
  },
  created() {
    this.getDatasource();
  },
};
</script>

<style scoped lang="scss">
.input {
  width: 200px;
}
</style>



